import { Box, Button, Typography, SvgIcon } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
//Material UI icons
import Call from '@material-ui/icons/Call';
import Email from '@material-ui/icons/Email';
import Facebook from '@material-ui/icons/Facebook';
import Instagram from '@material-ui/icons/Instagram';
import Telegram from '@material-ui/icons/Telegram';
import Twitter from '@material-ui/icons/Twitter';
import YouTube from '@material-ui/icons/YouTube';

import React from 'react';
//React helmet
import { Helmet } from 'react-helmet';

import {
  phoneNumber,
  email,
  emailSubject,
  facebookPage,
  instagramPage,
  twitterPage,
  telegramChannel,
  youTubeChannel,
} from '../config/appConfig';

export default function AboutUs() {
  const classes = useStyles();

  return (
    <React.Fragment>
      <Helmet>
        <title>{`About Me - ELearnBD`}</title>
      </Helmet>
      <Box
        maxWidth={960}
        margin="auto"
        p={4}
        bgcolor="background.paper"
        textAlign="center">
        <Typography variant="h4" color="textPrimary">
          Hello, I am
        </Typography>
        <Typography variant="h2" color="secondary" gutterBottom>
          Anisuzzaman Shayak
        </Typography>
        <Typography>
          I am a software developer. I work with Node.js, React.js and stuff. I
          built this project as a hobby.
        </Typography>
        <Typography gutterBottom>
          If you liked this app, please let me know...
        </Typography>
        <Box my={2} p={2} bgcolor="primary.main">
          <Button
            component="a"
            href={`tel:${phoneNumber}`}
            startIcon={<Call />}
            variant="text"
            className={classes.aboutLinButton}>
            {phoneNumber}
          </Button>
          <Button
            component="a"
            href={`mailto:${email}?Subject=${emailSubject}`}
            startIcon={<Email />}
            variant="text"
            className={classes.aboutLinButton}>
            {email}
          </Button>
          <br />
          <Button
            component="a"
            href={facebookPage}
            rel="noopener noreferrer"
            target="_blank"
            startIcon={<Facebook />}
            variant="text"
            className={classes.aboutLinButton}>
            Facebook
          </Button>
          <Button
            component="a"
            href={instagramPage}
            rel="noopener noreferrer"
            target="_blank"
            startIcon={<Instagram />}
            variant="text"
            className={classes.aboutLinButton}>
            Instagram
          </Button>
          <Button
            component="a"
            href={twitterPage}
            rel="noopener noreferrer"
            target="_blank"
            startIcon={<Twitter />}
            variant="text"
            className={classes.aboutLinButton}>
            Twitter
          </Button>
          <Button
            component="a"
            href={youTubeChannel}
            rel="noopener noreferrer"
            target="_blank"
            startIcon={<YouTube />}
            variant="text"
            className={classes.aboutLinButton}>
            YouTube
          </Button>{' '}
          <Button
            component="a"
            href={telegramChannel}
            rel="noopener noreferrer"
            target="_blank"
            startIcon={<Telegram />}
            variant="text"
            className={classes.aboutLinButton}>
            Telegram
          </Button>
        </Box>
        <hr />
        <Typography variant="h3" color="primary">
          THANK YOU!
        </Typography>
        <hr />
      </Box>
    </React.Fragment>
  );
}

const useStyles = makeStyles((theme) => ({
  aboutLinButton: {
    marginInline: theme.spacing(2),
    textTransform: 'none',
    color: theme.palette.primary.contrastText,
    '&:hover': { transform: 'scale(1.1)' },
  },
}));
