import { useLocation } from 'react-router';
/**
 * This function returns a specific parameter from the url\
 * It splits the url into an array and returns the parameter from the selected position
 *
 * @param {number} position from which to return the parameter
 * @param {string} [rootRoute] [optional] root route of the app, usually `'/'`
 * @returns {string} value of the parameter at the selected `position`
 *
 * Suppose, url is /home/books/bookname\
 * The parameters array will be `["home", "books", "bookname"]`\
 * So, to get the the book name from this url :
 * @example
 * const bookName = useUrlParam(2);
 */
const useUrlParam = (position = 0, rootRoute = '/') => {
	const levels = useLocation().pathname.replace(rootRoute, '').split('/');
	return levels[position];
};

export default useUrlParam;
